<template>
  <form class="image-options" name="image-options">
    <h2 class="image-options__heading">Конструктор худи<br/>от мамкупи</h2>
    <div class="image-options__info-block">
      <p class="image-options__info-composition"><span>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Animi commodi, dignissimos dolores dolorum, error eveniet facere labore magni molestias nisi provident similique, sint vitae. Nihil officia repellat sequi soluta voluptates.</span></p>
      <p class="image-options__info-price">3000 руб.</p>
    </div>
    <div class="hoodie-size">
      <div class="hoodie-size__select-label-wrapper">
        <label class="hoodie-size__select-label" for="hoodie-size">Размер: </label>
        <button class="hoodie-size__btn" type="button" @click="openTableOfSizes">
          <span class="hoodie-size__btn-inner">
            <v-svgicon class-name="hoodie-size__btn-icon" icon-name="icons/ruler" />
            <span class="hoodie-size__btn-text">Таблица размеров</span>
          </span>
        </button>
      </div>
      <select class="hoodie-size__select" name="hoodie-size" id="hoodie-size">
        <option
          v-for="(item, i) in ['xs', 's', 'm', 'l', 'xl', 'xxl']"
          :value="`hoodie-size-${item}`"
          :key="`hoodie-size-item-${i}`"
          :selected="i === 0"
        >
          {{ item.toUpperCase() }}
        </option>
      </select>
    </div>
    <div class="hoodie-size">
      <div class="hoodie-size__select-label-wrapper">
        <label class="hoodie-size__select-label" for="hoodie-length">Длина: </label>
      </div>
      <select class="hoodie-size__select" name="hoodie-length" id="hoodie-length" v-model="hoodieSize">
        <option
          v-for="(item, i) in ['Обычное', 'Укороченное', 'Удлинённое']"
          :value="item"
          :key="`hoodie-size-item-${i}`"
          :selected="i === 0"
        >
          {{ item }}
        </option>
      </select>
    </div>
    <div class="image-options__category">
      <h3 class="image-options__category-heading">Цвета</h3>
      <ul class="colors__list">
        <li class="colors__item" v-for="item in colors" :key="item.colorId">
          <input
            class="colors__item-checkbox visuallyhidden"
            type="radio"
            :disabled="checkDisabled(item.colorId)"
            :id="item.colorId"
            :value="item.color"
            name="colors"
            ref="checkbox"
            :checked="activeLayerColor === item.colorId"
            @change="setActiveLayerColor(item.colorId)"
          >
          <label
            class="colors__item-label"
            :for="item.colorId"
            :style="{backgroundColor: `#${item.colorId}`}"
            @click="warningCheck(item.colorId)"
          >
            {{ item.color }}
          </label>
        </li>
      </ul>
      <p class="colors__checked-list">
        Использовано цветов: {{ pickedColorsAmount }} / {{ maxColors }}
      </p>
    </div>
    <button class="btn image-options__add-btn">Добавить в корзину</button>
  </form>
</template>

