      hoodieShortBack: '<svg xmlns="http://www.w3.org/2000/svg" id="svgImage" data-angle="back" width="434" height="479" fill="none" viewBox="0 0 434 479"><g class="hoodie-short-back"><path fill="#CCC" d="M303.757 58.86c-1.899-.664-1.757 4.817-4.257 11.117-2.5 6.3-2.391 9.236-4 14.523-3.5 11.5-4.5 21-13 34.5l-24 13.5c-10.959 2.693-22.989 9.386-41.708 9.5H216.74c-18.715-.114-27.282-8.367-38.24-11.059l-15-7.941-13-10c-9.5-18-11-26.5-15-36.723-2-9.277-.547-19.209-5.668-17.436-9.245 4.097-18.775 8.448-26.403 12.218-3.933 1.945-7.366 3.732-9.943 5.219-2.535 1.463-4.337 2.879-4.434 2.967-.219.245-.467.462-.74.646l-.027.02-.012.007c-11.644 7.846-23.972 26.065-34.892 51.023-10.92 24.958-20.625 56.573-28.207 91.174-14.933 68.142-21.729 81.179-14.055 144.923 12.654 1.488 36.492 3.951 49.71 3.845 1.863-39.459 2.335-18.117 5.831-57.913 3.577-40.724 10.064-75.644 27.527-92.945a4.003 4.003 0 014.701-1.339 4.002 4.002 0 012.518 3.753c.229 49.739 2.415 72.054 2.01 129.97 28.293 14.247 68.498 14.786 112.604 14.587.248-.009.496.005.742.042.25-.038.503-.052.756-.042 44.105.199 84.31-.341 112.603-14.587-.404-57.671 1.762-79.575 2.007-129.304a4.01 4.01 0 011.131-3.567 3.998 3.998 0 013.584-1.072 3.999 3.999 0 012.476 1.531c17.485 17.292 23.982 52.228 27.561 92.973 3.496 39.796 3.462 18.454 5.324 57.913 13.215.107 37.051-2.356 49.709-3.845 7.674-63.744 1.386-76.781-13.547-144.923-7.582-34.601-17.284-66.216-28.205-91.174-10.828-24.748-23.05-42.855-34.607-50.808a4.005 4.005 0 01-.762-.577 4 4 0 01-.348-.343c-.152-.131-1.901-1.496-4.396-2.936-2.58-1.488-6.021-3.28-9.959-5.226-7.61-3.76-17.111-8.104-26.332-12.192z" class="layer main"/><g fill="#CCC" class="layer bottom"><path d="M107.287 362c29.917 12.107 68.176 11.958 108.767 11.775.24.008.48-.007.717-.043.234.036.47.05.707.043 40.59.183 78.85.331 108.767-11.775-.744 12.376-1.054 27.613-1.137 31.717l-.009.439c-4.684.851-55.374 9.933-107.951 8.738a3.996 3.996 0 00-.289-.007 4.113 4.113 0 00-.471.007c-52.579 1.196-103.263-7.887-107.947-8.738l