    }
  },
  methods: {
    setActiveLayerColor(color) {
      this.$store.commit('setActiveLayerColor', color)
      this.markPickedColor()
    },
    markPickedColor() {
      const pickedColors = this.$store.state.pickedColors
      this.$refs.checkbox.forEach(checkbox => {
        if (pickedColors.has(checkbox.id)) {
          checkbox.nextElementSibling.classList.add('colors__item-label--picked')
        } else if (checkbox.nextElementSibling.classList.contains('colors__item-label--picked')) {
          checkbox.nextElementSibling.classList.remove('colors__item-label--picked')
        }
      })
    },
    checkDisabled(colorId) {
      const state = this.$store.state
      if (state.activeLayer) {
        const activeLayerFill = state.clothesLayers[state.activeLayer].fill
        return !activeLayerFill && this.pickedColorsAmount === 3 && !state.pickedColors.has(colorId)
      } else {
        return !state.activeLayer
      }
    },
    openTableOfSizes() {
      this.$store.commit('toggleTableOfSizes')
      document.body.classList.add('blocked')
    },
    warningCheck(color) {
      const pickedColors = new Set(this.$store.state.pickedColors)
      pickedColors.add(color)
      if (pickedColors.size > 3) {
        document.body.classList.add('blocked')
        this.$store.commit('toggleColorRestriction')
      }
    },
    changeSize() {
      const angle = !!document.getElementById('svgImage').dataset.angle
      let hoodieSvg
      switch (this.hoodieSize) {
        case 'Обычное':
          hoodieSvg = angle ? this.hoodieRegularBack : this.hoodieRegular
          break
        case 'Укороченное':
          hoodieSvg = angle ? this.hoodieShortBack : this.hoodieShort
          break
        case 'Удлинённое':
          hoodieSvg = angle ? this.hoodieLongBack : this.hoodieLong
          break
      }
      eventBus.$emit('changeHoodieLength', hoodieSvg)
    }
  },
  computed: {
    activeLayerColor() {
      const activeLayer = this.$store.state.clothesLayers[this.$store.state.activeLayer]
      if (activeLayer && activeLayer.fill) {
        return activeLayer.fill.slice(1)
      }
      return null
    },
    pickedColorsAmount() {
      return this.$store.state.pickedColors.size
    },
    maxColors() {
      return this.$store.state.maxColors
    }
  },
  watch: {
    hoodieSize() {
      this.changeSize()
    }
  },
  created() {
    eventBus.$on('pickedColorsChange', this.markPickedColor)
    eventBus.$on('changeAngle', () => {
      const angle = !!document.getElementById('svgImage').dataset.angle
      let hoodieSvg
      switch (this.hoodieSize) {
        case 'Обычное':
          hoodieSvg = angle ? this.hoodieRegular : this.hoodieRegularBack
          break
        case 'Укороченное':
          hoodieSvg = angle ? this.hoodieShort : this.hoodieShortBack
          break
        case 'Удлинённое':
          hoodieSvg = angle ? this.hoodieLong : this.hoodieLongBack
          break
      }
      eventBus.$emit('changeHoodieLength', hoodieSvg)
      this.$store.commit('changeStateAngle')
    })
  }
}
</script>

<style scoped lang="scss">
  .image-options {
    grid-area: controls;
    max-width: 400px;
    margin-left: 50px;
  }

  .image-options__heading {
    margin-bottom: 10px;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 45px;
    font-weight: 700;
    line-height: 1;
    color: #000;
