import Vue from 'vue'
import Vuex from 'vuex'
import { eventBus } from '@/main'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    colors: [
      { color: 'Красный', colorId: 'be2a28' },
      { color: 'Зелёный', colorId: '014844' },
      { color: 'Синий', colorId: '1c2f4d' },
      { color: 'Розовый', colorId: 'ffbab8' },
      { color: 'Голубой', colorId: 'a9bfd7' },
      { color: 'Жёлтый', colorId: 'f9d15f' },
      { color: 'Коричневый', colorId: '734933' },
      { color: 'Чёрный', colorId: '000000' },
      { color: 'Сиреневый', colorId: 'd4adc8' },
      { color: 'Фиолетовый', colorId: '542837' }
    ],
    maxColors: 3,
    pickedColors: new Set(),
    activeLayer: null,
    clothesLayers: {},
    options: {
      hood: true,
      laces: true,
      pocket: true
    },
    angle: 'front',
    application: {
      front: {
        text: {
          text: '',
          style: ''
        },
        image: {
          image: '',
          type: '',
          size: ''
        }
      },
      back: {
        text: {
          text: '',
          style: ''
        },
        image: {
          image: '',
          type: '',
          size: ''
        }
      }
    },
    modals: {
      tableOfSizes: {
        isActive: false
      },
      applicationFrontText: {
        isActive: false
      },
      applicationFrontImage: {
        isActive: false
      },
      colorsRestrictionWarning: {
        isActive: false
      }
    }
  },
  mutations: {
    setImageLayers(state, layers) {
      layers.forEach(layer => {
        const layerName = layer.classList[1]

        Vue.set(
          state.clothesLayers,
          layerName,
          {
            layer: layer,
            fill: null
          }
        )
      })
    },
    setActiveLayer(state, payload) {
      state.activeLayer = payload
    },
    setActiveLayerColor(state, color) {
      const activeLayer = state.clothesLayers[state.activeLayer]
      const colorsSet = new Set()
      const colorsArray = []
      for (const layer in state.clothesLayers) {
        if (state.clothesLayers[layer].fill) {
          colorsSet.add(state.clothesLayers[layer].fill)
          colorsArray.push(state.clothesLayers[layer].fill)
        }
      }
      if (!activeLayer.fill && state.pickedColors.size === 3 && !state.pickedColors.has(color) && colorsSet.size === 3) {
        return
      }
      if (activeLayer.fill) {
        colorsArray.push(`#${color}`)
        const colorsArraySet = new Set(colorsArray)
        if (colorsArraySet.size === 4) {
          return
        }

        const newColorsSet = new Set([...state.pickedColors])
        newColorsSet.delete(activeLayer.fill.slice(1))
        state.pickedColors = new Set([...newColorsSet])
      }
      activeLayer.layer.style.fill = `#${color}`
      activeLayer.fill = `#${color}`

      for (const key in state.clothesLayers) {
        if (state.clothesLayers[key].fill) {
          state.pickedColors = new Set([...state.pickedColors]).add(state.clothesLayers[key].fill.slice(1))
        }
      }
    },
    refreshState(state) {
      state.pickedColors = new Set()
      state.activeLayer = null
      for (const layer in state.clothesLayers) {
        Vue.set(
          state.clothesLayers,
          layer,
          {
            layer: state.clothesLayers[layer].layer,
            fill: null
          }
        )
        state.clothesLayers[layer].layer.removeAttribute('style')
      }
      document.querySelector('.layer--active').classList.remove('layer--active')
      document.querySelectorAll('.colors__item-label--picked').forEach(item => {
        item.classList.remove('colors__item-label--picked')
      })
    },
    changeOptions(state, layer) {
      const svgLayer = document.getElementById('svgImage').querySelector(`.${layer}`)
      const svgLayerDisplay = getComputedStyle(document.getElementById('svgImage').querySelector(`.${layer}`)).display
      if (layer === 'hood' && state.options.laces) {
        const svgLayerLaces = document.getElementById('svgImage').querySelector('.laces')
        if (svgLayerDisplay === 'inline') {
          const colorsSet = new Set()
          const colorsArray = []
          for (const layer in state.clothesLayers) {
            if (state.clothesLayers[layer].fill) {
              colorsSet.add(state.clothesLayers[layer].fill)
              colorsArray.push(state.clothesLayers[layer].fill)
            }
          }
          ['hood', 'laces'].forEach(layer => {
            const colorIndex = colorsArray.indexOf(state.clothesLayers[layer].fill)
            if (colorIndex !== -1) {
              colorsArray.splice(colorIndex, 1)
            }
            if (new Set(colorsArray).size < colorsSet.size) {
              const tempSet = state.pickedColors
              tempSet.delete(state.clothesLayers[layer].fill.slice(1))
              state.pickedColors = new Set(tempSet)
            }
          })
          eventBus.$emit('pickedColorsChange')
          state.clothesLayers.hood.layer.removeAttribute('style')
          state.clothesLayers.laces.layer.removeAttribute('style')
          svgLayer.style.display = 'none'
          svgLayerLaces.style.display = 'none'
          document.querySelector('.layer.cash').style.display = 'inline'
          state.clothesLayers.hood.fill = null
          state.clothesLayers.laces.fill = null
        } else {
          svgLayer.style.display = 'inline'
          svgLayerLaces.style.display = 'inline'
          document.querySelector('.layer.cash').style.display = 'none'
        }
        state.options.laces = false
        state.options.hood = false
      } else if (layer !== 'pocket' && layer !== 'laces') {
        if (svgLayerDisplay === 'inline') {
          svgLayer.style.display = 'none'
          document.querySelector('.layer.cash').style.display = 'inline'
        } else {
          svgLayer.style.display = 'inline'
          document.querySelector('.layer.cash').style.display = 'none'
        }
        state.options[layer] = !state.options[layer]
      } else {
        if (svgLayerDisplay === 'inline') {
          svgLayer.style.display = 'none'
        } else {
          svgLayer.style.display = 'inline'
        }
        state.options[layer] = !state.options[layer]
      }
    },
    showModalApplication(state) {
      state.modals.applicationFrontText.isActive = true
    },
    setApplicationText(state, payload) {
      state.application[state.angle].text.text = payload[0]
      state.application[state.angle].text.style = payload[1]
    },
    setApplicationImage(state, payload) {
      state.application[state.angle].image.image = payload.image
      state.application[state.angle].image.type = payload.type
      state.application[state.angle].image.size = payload.size
    },
    toggleApplicationModals(state, modal) {
      if (modal === 'step-text') {
        state.modals.applicationFrontText.isActive = true
        state.modals.applicationFrontImage.isActive = false
      } else if (modal === 'step-image') {
        state.modals.applicationFrontText.isActive = false
        state.modals.applicationFrontImage.isActive = true
      } else {
        state.modals.applicationFrontText.isActive = false
        state.modals.applicationFrontImage.isActive = false
      }
    },
    toggleTableOfSizes(state) {
      state.modals.tableOfSizes.isActive = !state.modals.tableOfSizes.isActive
    },
    toggleColorRestriction(state) {
      state.modals.colorsRestrictionWarning.isActive = !state.modals.colorsRestrictionWarning.isActive
    },
    setLayersColors(state, layers) {
      layers.forEach(layer => {
        const layerName = layer.classList[1]
        layer.style.fill = state.clothesLayers[layerName].fill

        Vue.set(
          state.clothesLayers,
          layerName,
          {
            layer: layer,
            fill: state.clothesLayers[layerName].fill
          }
        )
      })
    },
    changeStateAngle(state) {
      state.angle = state.angle === 'front' ? 'back' : 'front'
    }
  },
  actions: {
  },
  getters: {
  }
})
